/****************************************/
/********** ANIMATION DURATION **********/
/****************************************/


[class*="animate-"][class*="tn"]:hover,
[class*="animate-"][class*="tn"]:focus,
[class*="animate-"][class*="tn"]:active,
*:hover > [class*="animated-"][class*="tn"],
*:focus > [class*="animated-"][class*="tn"],
*:active > [class*="animated-"][class*="tn"],
[class*="radio-"][class*="tn"]:checked ~ [class*="target-"][class*="tn"],
[class*="show-"][class*="tn"],
[class*="shown-"][class*="tn"] > * {
    animation-duration: var(--animation-duration-tn);
}

