/***********************************************/
/********** ANIMATION TIMING FUNCTION **********/
/***********************************************/


[class*="animate-"][class*="in"]:hover,
[class*="animate-"][class*="in"]:focus,
[class*="animate-"][class*="in"]:active,
*:hover > [class*="animated-"][class*="in"],
*:focus > [class*="animated-"][class*="in"],
*:active > [class*="animated-"][class*="in"],
[class*="radio-"][class*="in"]:checked ~ [class*="target-"][class*="in"],
[class*="show-"][class*="in"],
[class*="shown-"][class*="in"] > * {
    animation-timing-function: ease-in;
}

[class*="animate-"][class*="out"]:hover,
[class*="animate-"][class*="out"]:focus,
[class*="animate-"][class*="out"]:active,
*:hover > [class*="animated-"][class*="out"],
*:focus > [class*="animated-"][class*="out"],
*:active > [class*="animated-"][class*="out"],
[class*="radio-"][class*="out"]:checked ~ [class*="target-"][class*="out"],
[class*="show-"][class*="out"],
[class*="shown-"][class*="out"] > * {
    animation-timing-function: ease-out;
}

[class*="animate-"][class*="ease"]:hover,
[class*="animate-"][class*="ease"]:focus,
[class*="animate-"][class*="ease"]:active,
*:hover > [class*="animated-"][class*="ease"],
*:focus > [class*="animated-"][class*="ease"],
*:active > [class*="animated-"][class*="ease"],
[class*="radio-"][class*="ease"]:checked ~ [class*="target-"][class*="ease"],
[class*="show-"][class*="ease"],
[class*="shown-"][class*="ease"] > * {
    animation-timing-function: ease-in-out;
}

[class*="animate-"][class*="linear"]:hover,
[class*="animate-"][class*="linear"]:focus,
[class*="animate-"][class*="linear"]:active,
*:hover > [class*="animated-"][class*="linear"],
*:focus > [class*="animated-"][class*="linear"],
*:active > [class*="animated-"][class*="linear"],
[class*="radio-"][class*="linear"]:checked ~ [class*="target-"][class*="linear"],
[class*="show-"][class*="linear"],
[class*="shown-"][class*="linear"] > * {
    animation-timing-function: linear;
}

[class*="animate-"][class*="start"]:hover,
[class*="animate-"][class*="start"]:focus,
[class*="animate-"][class*="start"]:active,
*:hover > [class*="animated-"][class*="start"],
*:focus > [class*="animated-"][class*="start"],
*:active > [class*="animated-"][class*="start"],
[class*="radio-"][class*="start"]:checked ~ [class*="target-"][class*="start"],
[class*="show-"][class*="start"],
[class*="shown-"][class*="start"] > * {
    animation-timing-function: step-start;
}

[class*="animate-"][class*="end"]:hover,
[class*="animate-"][class*="end"]:focus,
[class*="animate-"][class*="end"]:active,
*:hover > [class*="animated-"][class*="end"],
*:focus > [class*="animated-"][class*="end"],
*:active > [class*="animated-"][class*="end"],
[class*="radio-"][class*="end"]:checked ~ [class*="target-"][class*="end"],
[class*="show-"][class*="end"],
[class*="shown-"][class*="end"] > * {
    animation-timing-function: step-end;
}

[class*="animate-"][class*="steps"]:hover,
[class*="animate-"][class*="steps"]:focus,
[class*="animate-"][class*="steps"]:active,
*:hover > [class*="animated-"][class*="steps"],
*:focus > [class*="animated-"][class*="steps"],
*:active > [class*="animated-"][class*="steps"],
[class*="radio-"][class*="steps"]:checked ~ [class*="target-"][class*="steps"],
[class*="show-"][class*="steps"],
[class*="shown-"][class*="steps"] > * {
