}

[class*="animate-"][class*="bezier"]:hover,
[class*="animate-"][class*="bezier"]:focus,
[class*="animate-"][class*="bezier"]:active,
*:hover > [class*="animated-"][class*="bezier"],
*:focus > [class*="animated-"][class*="bezier"],
*:active > [class*="animated-"][class*="bezier"],
[class*="radio-"][class*="bezier"]:checked ~ [class*="target-"][class*="bezier"],
[class*="show-"][class*="bezier"],
[class*="shown-"][class*="bezier"] > * {
    animation-timing-function: cubic-bezier(var(--animation-timing-bezier));
}